.card {
    width: 200px;
    height: 100px;
    border-radius: 15px;
    border: 3px solid;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
    cursor: grab;
}

.can {
    width: 200px;
    height: 300px;
    border-radius: 10px;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
    cursor: grab;
}

.cards, .cans {
    display: flex;
    justify-content: center;
}